@import 'mixins';
.footer {
  background-color: rgb(6, 6, 88);
  @include flexi($display: flex,$flex-direction: column);
  width: 100%;
  overflow: hidden;
margin-top:1%;
  .line {
    border: 0.6px solid rgb(255, 255, 255);
    width: 100%;
  }
  &__formImg {
    display: flex;
    height: 300px;
    position: relative;
    .footer__formImg-imgLeft {
      margin-right: 1%;
      flex-basis: 25%;
      height: 100%;
      position: relative;
      .footer__img {
        position: absolute;
        @include size(90%, auto);
        z-index: 0;
      }
      .footer__img:nth-child(2) {
        top: -20%;
        left: -45%;
        opacity: 0.8;
      }
      .footer__img:nth-child(1) {
        top: -50%;
        right: 30%;
      }
    }
    .footer__formImg-form {
        @include flexi($display: flex, $justify-content: space-around);
      flex-wrap: wrap;
      width: 50%;
      margin-left: 1%;
      margin-right: 1%;

      .footer__formImg-text {
        border-radius: 25px/30px;
      }
      .footer__formImg-text.text {
        @include size(47%, 30px)
      
      }
      .footer__formImg-text.textArea {
        @include size(100%, 100px)
     
      }

      .allButton {
        padding: 5px 25px;
        background-color: rgb(192, 115, 15);
        border-radius: 25px/30px;
        border: 0;
        color: rgb(255, 255, 255);
        @include size(120px, 30px);
        text-align: center;
        margin: 0 0 0 auto;
      }
    }

    .footer__formImg-imgRight {
      flex-basis: 25%;
      height: 100%;
      margin-left: 1%;
      margin-right: 1%;
      position: relative;
      .footer__img {
        position: absolute;
        @include size(90%, auto);
        z-index: 0;
      }
      .footer__img:nth-child(1) {
        top: -20%;
        right: -20%;
      }
      .footer__img:nth-child(2) {
        top: -50%;
        right: -40%;
        opacity: 0.8;
      }
    }
  }
  .everyMiddleHeading {
    @include middleHeading;
    color: rgb(255, 255, 255);
  }
  .everySmallestHeading {
    text-align: center;
    margin-top: 1%;
    color: rgb(255, 255, 255);
  }
  .goBack {
    @include flexi($display: flex, $flex-direction: column);
    &__arrowUp {
        @include size(30px, auto);
      align-self: center;
      color: rgb(255, 255, 255);
    }
    &__everySmallestHeading {
      align-self: center;
      margin-top: 1%;
      margin-bottom: 1%;
      color: rgb(255, 255, 255);
    }
  }
}
